.btn-explore {
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: #968c8c00;
  color: #fff;
  padding: 8px 24px;
  transition: all 0.3s ease-in-out;
  display: inline-flex;
  align-items: center;
  gap: 8px;

  svg {
    fill: white;
    /* or your desired color */
  }
}

.btn-explore:hover {
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: #fff;
  color: #15142b !important;
  padding: 8px 24px;

  svg {
    fill: #15142b;
    /* or your desired color */
  }
}






.main-slider-div{
    background: #150F22;
}
.state-slider-container {
  padding: 2rem 0;
  position: relative;
  overflow: hidden;

  .slider-wrapper {
    width: 100%;
    overflow: hidden;
  }

  .slider {
    display: flex;
    transition: transform 0.6s ease-in-out;
  }

  .slide {
    display: flex;
    flex: 0 0 100%;
    justify-content: space-between;
    gap: 20px;
  }

  .state-card {
    background: linear-gradient(148.14deg,
        rgba(88, 71, 114, 0.55) -33.59%,
        rgba(47, 42, 56, 0.55) 79.09%);
    padding: 25px 35px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 12px;
    height: 100%;
    // width: 400px;
  }

  min-width: 220px;

  h2 {
    color: #fff;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-transform: capitalize;
    margin-bottom: 12px;
  }

  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    gap: 8px;

    span {
      width: 10px;
      height: 10px;
      background: #555;
      border-radius: 50%;
      display: inline-block;
      cursor: pointer;
      transition: background 0.3s;

      &.active {
        background: #fff;
      }
    }
  }
}

/* Default (>=1380px) is already good with your current code */

/* Up to 1200px (large tablets / small desktops) */
@media (max-width: 1200px) {
  .state-slider-container {
    .slide {
      gap: 16px;
    }
    .state-card {
      padding: 20px 24px;
      flex: 1 1 45%; /* 2 cards per row */
    }
    h2 {
      font-size: 16px;
    }
  }
}

/* Up to 992px (tablets landscape) */
@media (max-width: 992px) {
  .state-slider-container {
    .slide {
      flex-wrap: wrap; /* allow wrapping */
      justify-content: center;
    }
    .state-card {
      flex: 1 1 100%; /* 1 card per row */
      max-width: 90%;
      height: fit-content;
    }
    h2 {
      font-size: 16px;
      text-align: center;
    }
    .btn-explore {
      margin: 0 auto;
    }
  }
}

/* Up to 768px (tablets portrait) */
@media (max-width: 768px) {
  .state-slider-container {
    padding: 1.5rem 0;

    .slide {
      gap: 12px;
    }
    .state-card {
      padding: 16px 20px;
      border-radius: 6px;
    }
    h2 {
      font-size: 15px;
    }
  }
}

/* Up to 576px (phones) */
@media (max-width: 576px) {
  .state-slider-container {
    .state-card {
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 14px 16px;

      img {
        width: 100%;
        height: auto;
        max-width: 260px;
      }
    }
    h2 {
      font-size: 14px;
    }
    .btn-explore {
      width: 100%;
      justify-content: center;
    }
  }
}
